import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AlertService } from 'src/app/core/services';
import { ReviewsService } from '../../services/reviews.service';

@Component({
  selector: 'app-add-review',
  templateUrl: './add-review.component.html',
  styleUrls: ['./add-review.component.scss']
})
export class AddReviewComponent implements OnInit {
  addReviewForm: FormGroup
  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<AddReviewComponent>,
    private AlertService: AlertService,
    private ReviewsService: ReviewsService
  ) { }

  ngOnInit(): void {
    this.createAddReviewForm();
  }

  createAddReviewForm() {
    this.addReviewForm = new FormGroup({
      text: new FormControl("", [Validators.required]),
      rate: new FormControl(1, []),
      anonynom: new FormControl(false, [])
    })
  }

  onRatingSet(event) {
    this.addReviewForm.patchValue({ rating: event })
  }

  checkAnonynom(checked) {
    this.addReviewForm.patchValue({ anonynom: checked })
  }

  addReview() {
    if (!this.addReviewForm.valid) {
      this.addReviewForm.markAllAsTouched()
      return this.AlertService.error("error in form")
    }
    //add review then close dailog and send success add or not to update review in product page
    let review = {
      categoryId: this.data['categoryId'],
      versionId: Number(this.data['versionId']),
      userId: localStorage.getItem("id"),
      text: this.addReviewForm.value.text,
      rating: this.addReviewForm.value.rate,
      anonynom: this.addReviewForm.value.anonynom
    }
    this.ReviewsService.addReview(review).subscribe(data => {
      this.dialogRef.close('Review Added')
    })
  }

}
