import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApiResponse } from 'src/app/models/api-response.model';
import { environment } from 'src/environments/environment';
import { Reviews } from '../models/reviews.model';

@Injectable({
  providedIn: 'root'
})
export class ReviewsService {

  constructor(private http: HttpClient) { }

  getReviewsForProduct(CategoryId: number, VersionId: number, UserId: string) {
    return this.http.get<ApiResponse<Reviews[]>>(`${environment.baseUrl}Review?CategoryId=${CategoryId}&VersionId=${VersionId}&NoPaging=true&UserId=${UserId}`)
  }


  addReview(review) {
    return this.http.post(`${environment.baseUrl}Review`, review)
  }
}


